body {
  background: #c4183c88;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
  height: 100vh;
}
.App {
  background-color: #f9f9f9;
  text-align: center;
  max-width: 1200px;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.App-Bar {
  width: 100%;
  height: 40px;
  background-color: #222;
  color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding-left: 20px;

}

.fm-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 14px;
  width: 120px;
  height: 100px;
  background: #f2f2f2;
  padding: 20px;
  margin: 10px;
  cursor: pointer;
  border: 1px solid #f8f8f8;
}

.fm-item:hover,.fm-item.selected {
  border: 1px solid #c4183c;
}

.error {
  color: #f00;
  font-size: 12px;
  margin-left: 10px;
}

button {
  margin-left: 10px;
  border: 0;
  background: transparent;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 0;
  height: 20px;
  border-color: transparent transparent transparent #fff;
  -webkit-transition: 100ms all ease;
  -o-transition: 100ms all ease;
  transition: 100ms all ease;
  cursor: pointer;
  border-style: solid;
  border-width: 10px 0 10px 15px;
}
button.paused {
  border-style: double;
  border-width: 0px 0 0px 15px;
}
button:hover {
  border-color: transparent transparent transparent #c4183c;
}


